

import Vue from 'vue'
import FastClick from 'fastclick'
import VueRouter from 'vue-router'
import { sync } from 'vuex-router-sync'

import store from './store'
import App from './App'
import objectAssign from 'object-assign'
import { User, Version, Token, Access, Website, MenuData } from '#/storage'
import ENV from 'env'
import Util from '#/util'
import AjaxPlugin from 'vux/src/plugins/ajax/index.js'
import WechatPlugin from 'vux/src/plugins/wechat/index.js'
import BusPlugin from 'vux/src/plugins/bus/index.js'
import LoadingPlugin from 'vux/src/plugins/loading/index.js'
import ToastPlugin from 'vux/src/plugins/toast/index.js'
import AlertPlugin from 'vux/src/plugins/alert/index.js'
import ConfirmPlugin from 'vux/src/plugins/confirm/index.js'

import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import VideoPlayer from 'vue-video-player'
import 'vue-video-player/src/custom-theme.css'
import 'video.js/dist/video-js.css'

Vue.use(ElementUI)
Vue.use(VueRouter)
Vue.use(Util)
Vue.use(AjaxPlugin)
Vue.use(WechatPlugin)
Vue.use(BusPlugin)
Vue.use(LoadingPlugin)
Vue.use(ToastPlugin)
Vue.use(AlertPlugin)
Vue.use(ConfirmPlugin)
Vue.use(VideoPlayer)
require('es6-promise').polyfill()

console.log('env.debugmode', ENV.DebugMode)
if (ENV.DebugMode) {
const VConsole = require('vconsole')
const vConsole = new VConsole()
vConsole.setOption({maxLogNumber: 5000})
}

const CancelToken = AjaxPlugin.$http.CancelToken
const vuxLocales = require('./locales/all.yml')
const componentsLocales = require('./locales/components.yml')


const finalLocales = {
'en': objectAssign(vuxLocales['en'], componentsLocales['en']),
'zh-CN': objectAssign(vuxLocales['zh-CN'], componentsLocales['zh-CN'])
}

for (let i in finalLocales) {
Vue.i18n.add(i, finalLocales[i])
}
Vue.i18n.set('zh-CN')

FastClick.attach(document.body)

Vue.config.productionTip = false


const routes = [{
  path: '/home',
  name: 'tHome',
  component: () => import('./pages/Home.vue').then(m => m.default)
},
{
  path: '/aboutus',
  name: 'tAboutus',
  component: () => import('./pages/Aboutus.vue').then(m => m.default)
},
{
  path: '/code',
  name: 'tCode',
  component: () => import('./pages/Code.vue').then(m => m.default)
},
{
  path: '/news',
  name: 'tNews',
  component: () => import('./pages/News.vue').then(m => m.default)
},
{
  path: '/newslist',
  name: 'tNewslist',
  component: () => import('./pages/Newslist.vue').then(m => m.default)
},
{
  path: '/product',
  name: 'tProduct',
  component: () => import('./pages/Product.vue').then(m => m.default)
},
{
  path: '/productlist',
  name: 'tProductlist',
  component: () => import('./pages/Productlist.vue').then(m => m.default)
},
{
  path: '/register',
  name: 'tRegister',
  component: () => import('./pages/Register.vue').then(m => m.default)
},
{
  path: '/sos',
  name: 'tSos',
  component: () => import('./pages/Sos.vue').then(m => m.default)
}]

routes.push({
path: '/',
name: 'tHome',
component: () => import('./pages/Home').then(m => m.default)
})

const router = new VueRouter({
mode: 'history',
base: '/companybsa/',
routes: routes
})

sync(store, router)

const history = window.sessionStorage
history.clear()
let historyCount = history.getItem('count') * 1 || 0
history.setItem('/', 0)
let isPush = false
let endTime = Date.now()
let methods = ['push', 'go', 'replace', 'forward', 'back']

document.addEventListener('touchend', () => {
endTime = Date.now()
})
methods.forEach(key => {
let method = router[key].bind(router)
router[key] = function (...args) {
isPush = true
method.apply(null, args)
}
})

router.beforeEach(function (to, from, next) {
store.commit('updateLoadingStatus', {isLoading: true})
const toIndex = history.getItem(to.path)
const fromIndex = history.getItem(from.path)

if (toIndex) {
if (!fromIndex || parseInt(toIndex, 10) > parseInt(fromIndex, 10) || (toIndex === '0' && fromIndex === '0')) {
store.commit('updateDirection', {direction: 'forward'})
} else {

if (!isPush && (Date.now() - endTime) < 377) {
store.commit('updateDirection', {direction: ''})
} else {
store.commit('updateDirection', { direction: 'reverse' })
}
}
} else {
++historyCount
history.setItem('count', historyCount)
to.path !== '/' && history.setItem(to.path, historyCount)
store.commit('updateDirection', {direction: 'forward'})
}

if (/\/http/.test(to.path)) {
let url = to.path.split('http')[1]
window.location.href = `http${url}`
} else {
next()
}
})

router.afterEach(function (to) {
isPush = false
store.commit('updateLoadingStatus', {isLoading: false})
})

let pendings = []
let cancelAllPendings = () => {
for (let p of pendings) {
console.info(`canceled request: ${p.u}`)
p.f()
}
pendings = []
}


const rExcludeUrls = ENV.NoAccessUrls.map(url => {

return RegExp(url.replace(/\//g, '\\/').replace(/\*/g, '.*').replace(/\?/g, '\\?'))
})
const matchExclude = url => {
for (let i = 0; i < rExcludeUrls.length; i++) {
if (rExcludeUrls[i].test(url)) {
return true
}
}
return false
}



Vue.http.interceptors.request.use(config => {
console.log('执行ajax请求', config.url)
console.log(Token.get())
console.log(matchExclude(config.url))
if (matchExclude(config.url)) {
config.cancelToken = new CancelToken(c => {
pendings.push({ u: config.url + '&' + config.method, f: c })
})
const token = Token.get()
if (Token.isExpired()) {
cancelAllPendings(config)
router.replace({path: '/Code'})
} else {
config.headers['Authorization'] = `Bearer ${token.token}`
}
}
return config
}, error => {
return Promise.reject(error)
})


Vue.http.interceptors.response.use(response => {


return response
}, error => {
console.log('请求结束错误了')
console.log(error)
})

const clearCache = () => {
const hostname = document.location.hostname
let hname = hostname.split('.')[0]
if (ENV.isKf) hname = ENV.preName
if (Website.get() !== hname) {
Token.remove()
User.remove()
Access.remove()
Version.remove()
Version.set(ENV.Version)
Website.remove()
Website.set(hname)
MenuData.remove()
}
}

const vue = new Vue({
store,
router,
render: h => h(App)
})

const render = () => {
vue.$mount('#app-box')
}

clearCache()










render()



