
import ViewBox from 'vux/src/components/view-box/index.vue'
import Loading from 'vux/src/components/loading/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import { mapState } from 'vuex'
import { MenuData, Factory, WebsiteParams } from '#/storage'
import ENV from 'env'
export default {
name: 'app',
provide () {
return {
reload: this.reload
}
},
directives: {
TransferDom
},
components: {
ViewBox,
Loading,
Tabbar,
TabbarItem
},
watch: {
path (path) {
if (path === '/component/demo') {
this.$router.replace('/demo')
}
},
'$route' (to, from) {

document.title = this.getTitle(to.path)
}
},
computed: {
...mapState({
route: state => state.route,
path: state => state.route.path,
isLoading: state => state.vux.isLoading,
direction: state => state.vux.direction,
toggleTabbar: state => state.vux.toggleTabbar
}),
leftOptions () {
return {
showBack: this.route.path !== '/'
}
},
rightOptions () {
return {
showMore: true
}
},















viewTransition () {
console.log(this.direction)
if (!this.direction) return ''
return 'vux-' + (this.direction === 'forward' ? 'in' : 'out')
}
},
methods: {
getTitle (path) {
let name = path.substr(1).replace(/([a-z])(.*)/, (match, p1, p2) => `t${p1.toUpperCase()}${p2}`)
if (name === '') {
name = 'tIndex'
}
const title = this.$t(name)
return title || ' '
},
getData () {
let hname = this.$util.getHostName()
this.$http.post(`${ENV.GxkApi}/api/Website/initData`, {
prefixdomain: hname
}).then(res => {
const data = res.data
if (data.flag) {
MenuData.set(data.channel)
Factory.set(data.factory)
WebsiteParams.set(data.paras)
} else {
if (this.$route.path !== '/sos') {
this.$router.replace('/sos')
}
}
})
},
globalTouch () {
this.$vux.loading.hide()
}
},
created () {
console.info('App Start Up')
document.title = this.$t('tIndex')
this.getData()

}
}



